.burger {
  position: absolute;
  width: 70px;
  height: 22px;
  cursor: pointer;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: none;
}

.burger img {
  display: block;
  float: left;
  width: 22px;
  height: 22px;
}

