.close {
  position: absolute;
  z-index: 5;
  top: 40px;
  right: 30px;
  width: 40px;
  height: 40px;
  background: #fff;
  border-radius: 50%;
  cursor: pointer;
  transition: linear 0.25s;
}

.close::before {
  position: absolute;
  content: "";
  top: 18px;
  left: 8px;
  width: 25px;
  height: 3px;
  background: #000;
  transform: rotate(45deg);
  transition: linear 0.25s;
}

.close::after {
  position: absolute;
  content: "";
  top: 18px;
  left: 8px;
  width: 25px;
  height: 3px;
  background: #000;
  transform: rotate(-45deg);
  transition: linear 0.25s;
}

.close.active__show::after {
  transform: rotate(45deg);
  background: #e01a11;
}

.close.active__show::before {
  transform: rotate(-45deg);
  background: #e01a11;
}

.close.active__hide::after {
  transform: rotate(0deg);
  background: #e01a11;
}

.close.active__hide::before {
  transform: rotate(0deg);
  background: #e01a11;
}

