}

.close_cst.Show {
  opacity: 1;
}

.modal.open-form {
  display: block;
}

.modal-form {
  position: absolute;
  z-index: 60;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  padding: 0 15px;
  background: #fff;
}

.modal-form form {
  position: relative;
  padding-top: 50px;
}

.modal-form form ::placeholder {
  color: #333;
}

#valid {
  position: absolute;
  width: 100%;
  top: 10px;
  left: 0;
  text-align: center;
}

#modal-form__name,
#modal-form__phone,
#modal-form__email {
  display: block;
  width: 100%;
  height: 50px;
  margin-bottom: 20px;
  border: 2px solid #e2e5e7;
  padding-left: 10px;
  color: #333;
}

.modal-form__btn {
  display: block;
  border: none;
  width: 100%;
  height: 50px;
  margin: 0 auto 20px;
  background: #e2e5e7;
  cursor: pointer;
  color: #333;
  font-size: 18px;
  font-family: "Oswald-Regular", sans-serif;
}

.modal-form__btn:hover {
  background: #d6d9db;
  text-decoration: underline;
}

/*------- thank-you -------*/

.studio-block {
  width: 100%;
  padding-top: 200px;
  text-align: center;
}

.studio-block a {
  display: inline-block;
  transition: linear 0.25s;
}

.studio-block a:hover {
  color: #e01a11;
}

/*------- thank-you -------*/

.thank-you {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #1a1b25;
}

.thank-you__text {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #fff;
}

.thank-you__text h1 {
  font-size: 50px;
  line-height: 52px;
}

.thank-you__text p {
  margin-top: 10px;
}

.thank-you__text a {
  display: inline-block;
  margin-top: 10px;
  text-decoration: underline;
  transition: linear 0.25s;
  color: #fff;
}

.thank-you__text a:hover {
  color: #ff0000;
}

