  a{
    display: inline-block;
    transition: linear 0.25s;
    &:hover{
      color: #e01a11;
    }
  }
}

/*------- thank-you -------*/

.thank-you{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #1a1b25;
}

.thank-you__text{
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #fff;
  h1{
    font-size: 50px;
    line-height: 52px;
    .sm-block({
      font-size: 40px;
      line-height: 42px;
    });
    .xs-block({
      font-size: 30px;
      line-height: 32px;
    });
  }
  p{
    margin-top: 10px;
  }
  a{
    display: inline-block;
    margin-top: 10px;
    text-decoration: underline;
    transition: linear 0.25s;
    color: #fff;
    &:hover{
      color: #ff0000;
    }
  }
}


